<template>
  <div class="account">
    <div class="top"></div>
    <van-nav-bar
      title="个人资料"
      left-text="返回"
      left-arrow
      :fixed="true"
      @click-left="back"
    />
    <div class="my-bg">
      <div
        class="bg"
        :style="{backgroundImage: `url(${bg})`}"
      >
      </div>
    </div>

    <div class="list-box">
      <van-cell title="头像">
        <div class="headPortrait fr">
          <img
            class="auto-img"
            :src="userInfo.userImg"
            alt=""
          >
          <van-uploader
            class="uploader-box"
            :after-read="uploadUserImg"
          />
        </div>
      </van-cell>
      <van-cell title="用户id">
        <div class="fr">{{userInfo.userId}}</div>
      </van-cell>
      <van-cell title="手机号">
        <div class="fr">{{userInfo.phone}}</div>
      </van-cell>
      <van-cell
        title="昵称"
        is-link
      >
        <div
          class="fr nicknameinp"
          @click="clickPopup"
        >
          {{userInfo.nickName}}
        </div>
        <van-popup
          v-model="ispopup"
          :style="{width:'80%'}"
          round
        >
          <div class="popup-box">
            <div class="popup-title">更改昵称</div>
            <input
              class="nickname"
              type="text"
              ref="inp"
              v-model="userInfo.nickName"
              @click="editTask"
              @change="modifyUserInfo('nickName','/updateNickName')"
            >
            <van-button
              color="#0c34ba"
              size="small"
              @click="clickPopup"
            >完成</van-button>
          </div>
        </van-popup>
      </van-cell>
      <div class="desc-box">
        <div class="desc-title">简介</div>
        <div>
          <textarea
            class="text-area"
            placeholder="这个家伙很懒，什么都没留下!"
            v-model="userInfo.desc"
            @change="modifyUserInfo('desc','/updateDesc')"
          ></textarea>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/account.less";
import { utils } from "../assets/js/Utils";
export default {
  data() {
    return {
      userInfo: {},
      maxSize: 1 * 1024 * 1024,
      ispopup: false,
      bg: "",
    };
  },

  created() {
    this.getUserInfo();
    // 获取用户背景图
    this.getBg();
  },

  methods: {
    // 返回
    back() {
      this.$router.go(-1);
    },

    // 获取个人资料
    getUserInfo() {
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/findAccountInfo",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "B001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 获取用户背景图片
    getBg() {
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        return;
      }

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "A001") {
            this.bg = result.data.result[0].userBg;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 上传头像
    uploadUserImg(file) {
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        return;
      }

      // 获取文件类型
      let type = file.file.type.split("/")[1];
      let imgTypes = ["png", "gif", "jpg", "jpeg"];
      // 验证文件类型
      // -1 就是找不到的时候
      if (imgTypes.indexOf(type) === -1) {
        this.$notify({
          message: "图片类型只支持" + imgTypes.join(","),
          color: "#fff",
          background: "#0c34ba",
        });
        return;
      }
      // 验证文件大小
      if (this.maxSize < file.file.size) {
        this.$notify({
          message: "上传文件大小不能超过" + this.maxSize / 1024 + "KB",
          color: "#fff",
          background: "#0c34ba",
        });
        return;
      }

      // 获取base64
      let base64 = file.content.replace(/data:image\/[A-Za-z]+;base64,/, "");

      // 参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        imgType: type,
        serverBase64Img: base64,
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "POST",
        url: "/updateAvatar",
        data,
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "H001") {
            this.userInfo.userImg = result.data.userImg;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },

    // 修改用户信息
    modifyUserInfo(key, url) {
      // 获取token字符串
      let tokenString = localStorage.getItem("TK");
      if (!tokenString) {
        return;
      }

      // 参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        [key]: this.userInfo[key],
      });

      setTimeout(() => {
        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0,
          loadingType: "spinner",
        });
        this.axios({
          method: "POST",
          url,
          data,
        })
          .then((result) => {
            this.$toast.clear();

            this.$toast(result.data.msg);
          })
          .catch((err) => {
            this.$toast.clear();
          });
      }, 800);
    },

    // 弹出层
    clickPopup() {
      this.ispopup = !this.ispopup;
    },

    // 点击inp获取焦点在文字后面
    editTask() {
      // input框聚焦
      this.$nextTick(() => {
        // 'selectionStart' 表示输入性元素selection起点的位置。
        // 'selectionEnd' 表示输入性元素selection末点的位置,都是 DOM 属性。
        let length = this.userInfo.nickName.length;
        this.$refs.inp.focus();
        this.$refs.inp.selectionStart = length;
        this.$refs.inp.selectionEnd = length;
      });
    },
  },
};
</script>
